  >
    <div id="featured-post-text" class="col-md-6 px-0">
      <h1 class="display-4">{{ article.title }}</h1>
      <p class="lead my-3">
        {{ article.summary }}
      </p>
      <p class="lead mb-0">
        <router-link :to="getArticleUrl(article)" class="text-white fw-bold"
