<script>
import Endpoints from "@/external/endpoints";
import { ArticleParams } from "@/external/ArticleParams";
export default {
  name: "FeaturedPost",
  data() {
    return {
      article: null,
    };
  },
  mounted() {
    let param = new ArticleParams();
    param.isFeatured = true;
    Endpoints.getArticles(param)
      .then((response) => (this.article = response.data.data[0]))
      .catch((err) => {
        this.errorMessage = err;
      });
  },
  methods: {
    getArticleUrl(article) {
      return "/articles/" + article.slug;
    },
  },
};
</script>

