export class ArticleParams {
    constructor() {
        this.pageNumber = 1;
        this.pageSize = 1;
        this.searchWord = null;
        this.isFeatured = null;
        this.isShowcased = null;
        this.isPublished = null;
    }
    toString() {

        let string = `?pageNumber=${this.pageNumber}&pageSize=${this.pageSize}`;

        if (this.searchWord != null) {
            string += `&searchWord=${this.searchWord}`;
        }

        if (this.isFeatured != null) {
            string += `&isFeatured=${this.isFeatured}`;
        }

        if (this.isShowcased != null) {
            string += `&isShowcased=${this.isShowcased}`;
        }

        if (this.isPublished != null) {
            string += `&isPublished=${this.isPublished}`;
        }

        return string;
    }
}