<template>
  <div
    class="
      row
      g-0
      border
      rounded
      overflow-hidden
      flex-md-row
      mb-4
      shadow-sm
      h-md-250
      position-relative
    "
  >
    <div class="col p-4 d-flex flex-column position-static">
      <strong class="d-inline-block mb-2 text-primary">{{ post.tag }}</strong>
      <h3 class="mb-0">{{ post.title }}</h3>
      <div class="mb-1 text-muted small-text">
        <i class="fas fa-eye" title="Views"></i> {{ post.viewCount }} /
        <i class="fas fa-comment" title="Comments"></i> 0 /
        {{ post.datePosted }}
      </div>
      <p class="card-text small-text mb-auto">{{ post.summary }}</p>
      <router-link :to="getArticleUrl(post)" class="stretched-link"
        >Continue reading</router-link
      >
    </div>
    <div class="col-auto d-none d-lg-block">
      <img
        v-if="post.summaryImage"
        :src="post.summaryImage"
        style="width: 200px"
      />
      <svg
        v-else
        class="bd-placeholder-img"
        width="200"
        height="250"
        xmlns="http://www.w3.org/2000/svg"
        role="img"
        aria-label="Placeholder: Thumbnail"
        preserveAspectRatio="xMidYMid slice"
        focusable="false"
      >
        <title>Placeholder</title>
        <rect width="100%" height="100%" fill="#55595c" />
        <text x="50%" y="50%" fill="#eceeef" dy=".3em">Thumbnail</text>
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  name: "PostSummary",
  props: {
    post: Object,
  },
  methods: {
    getArticleUrl(article) {
      return "/articles/" + article.slug;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.small-text {
  font-size: 16px !important;
}
.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.small-text {
  font-size: small;
  margin-top: 10px;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}
</style>
