      <i class="fas fa-comment" title="Comments"></i> 0 /
      {{ postData.datePosted }}
      by <a href="#">{{ postData.author }}</a>
    </div>
    <br />
    <div v-html="postData.body" />
  </article>
</template>

<script>
export default {
  name: "Post",
  props: {
    postData: Object,
  },
};
</script>

