<template>
  <div class="position-sticky" style="top: 2rem">
    <div class="p-4 mb-3 bg-light rounded" style="text-align: justify">
      <h4 class="">WELCOME</h4>
      <p class="mb-0">
        Do you like hiking and camping but all your friends prefer to stay in
        and watch TV?
      </p>
      <p>This is a blog about getting out and hiking alone.</p>
      <p>
        Stop by
        <a href="https://www.reddit.com/r/MuddyHiker/">r/MuddyHiker</a> and
        become part of the community.
      </p>
    </div>

    <div class="p-4">
      <h4 class="">TOP ARTICLES</h4>
      <ol class="list-unstyled mb-0">
        <li v-for="(article, x) in this.articles" :key="x">
          <router-link :to="'/articles/' + article.slug">
            {{ article.title }}
          </router-link>
        </li>
      </ol>
    </div>

    <div class="p-4">
      <h4 class="">COMMUNITY</h4>
      <ol class="list-unstyled">
        <li>
          <a href="https://www.instagram.com/hikermuddy/"
            ><i class="fab fa-instagram"></i> Instagram</a
          >
        </li>
        <li>
          <a href="https://www.reddit.com/r/MuddyHiker/"
            ><i class="fab fa-reddit"></i> Reddit</a
          >
        </li>
      </ol>
    </div>
  </div>
</template>

<script>
import Endpoints from "@/external/endpoints";
import { ArticleParams } from "@/external/ArticleParams";
export default {
  name: "Sidebar",
  data() {
    return {
      articles: [],
    };
  },
  props: {},
  mounted() {
    let param = new ArticleParams();
    param.pageSize = 10;
    Endpoints.getArticles(param).then(
      (response) => (this.articles = response.data.data)
    );
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  text-decoration: none;
}
</style>