const Urls = {

    muddyHiker: () => `https://localhost:44390/api`,
    article: (articleId) => `${Urls.muddyHiker()}/Articles/${articleId}`,
    articles: () => `${Urls.muddyHiker()}/Articles`,
    articleSearch: (params) => `${Urls.articles()}${params}`,
    articleSlug: (slug) => `${Urls.articles()}/name/${slug}`,
    links: (pageNumber, pageSize) => `${Urls.muddyHiker()}/Links?pageNumber=${pageNumber}&pageSize=${pageSize}`,
    tag: (tagId) => `${Urls.muddyHiker()}/Tags/${tagId}`,
    tags: () => `${Urls.muddyHiker()}/Tags`,
    publicImages: () => `/image-names.json`
}

export default Urls