import Urls from '@/external/urls'
import Auth from '@/external/auth'
import axios from "axios";
//import { ArticleParams } from "@/external/ArticleParams";

export default {
    getArticles(params) {
        return axios
            .get(Urls.articleSearch(params.toString()));
    },
    getArticleBySlug(slug) {
        return axios
            .get(Urls.articleSlug(slug));
    },
    getLinks(pageNumber = 1, pageSize = 1) {
        return axios
            .get(Urls.links(pageNumber, pageSize));
    },
    putArticle(articleId, article) {
        return axios
            .put(Urls.article(articleId), article,
                {
                    auth: Auth.credentials
                })
    },
    postArticle(article) {
        return axios
            .post(Urls.articles(), article,
                {
                    auth: Auth.credentials
                },
            )
    },
    deleteArticle(articleId) {
        return axios
            .delete(Urls.article(articleId),
                {
                    auth: Auth.credentials
                },
            )
    },
    getTags() {
        return axios
            .get(Urls.tags());
    },
    deleteTag(tagId) {
        return axios
            .delete(Urls.tag(tagId));
    },
    postTag(tag) {
        return axios
            .post(Urls.tags(), tag);
    },
    getImageNames() {
        return axios
            .get(Urls.publicImages());
    },
}