<template>
  <nav v-if="this.breadcrumbs" aria-label="breadcrumb">
    <ol class="breadcrumb">
      <li
        v-for="(breadcrumb, x) in this.breadcrumbs"
        :key="x"
        class="breadcrumb-item"
      >
        <router-link v-if="breadcrumb.link" :to="breadcrumb.link">{{
          breadcrumb.name
        }}</router-link>
        <span v-else>{{ breadcrumb.name }}</span>
      </li>
    </ol>
  </nav>
</template>

<script>
export default {
  name: "Breadcrumbs",
  data() {
    return {
      breadcrumbs: [],
      pageName: "",
    };
  },
  props: {
    overRide: Array,
  },
  mounted() {
    if (this.overRide) {
      this.breadcrumbs = this.$route.meta.breadcrumb;
    } else {
      this.breadcrumbs = this.$route.meta.breadcrumb;
    }

    //this.pageName = this.$route.name;
  },
};
</script>

