<template>
  <div class="row mb-2">
    <div v-for="(article, x) in articles" :key="x" class="col-md-6">
      <PostSummary v-bind:post="article" />
    </div>
  </div>
</template>

<script>
import Endpoints from "@/external/endpoints";
import PostSummary from "./PostSummary.vue";
import { ArticleParams } from "@/external/ArticleParams";
export default {
  name: "PostSummaryGrid",
  components: {
    PostSummary,
  },
  data() {
    return {
      articles: Object,
    };
  },
  mounted() {
    let param = new ArticleParams();
    param.isShowcased = true;
    param.pageSize = 10;
    Endpoints.getArticles(param)
      .then((response) => (this.articles = response.data.data))
      .catch((err) => {
        this.errorMessage = err;
      });
  },
};
</script>

