<template>
  <div class="container">
    <Breadcrumbs />
    <div class="row g-5">
      <div class="col-md-8 blog-post">
        <h2>Usefull Links</h2>
        <p>This page has some links to other websites that we find usefull.</p>

        <div class="list-group">
          <a
            v-for="(link, x) in this.links"
            :key="x"
            :href="link.url"
            class="list-group-item list-group-item-action"
            target="_blank"
          >
            <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1">{{ link.title }}</h5>
            </div>
            <p class="mb-1">
              {{ link.description }}
            </p>
            <small
              ><a :href="link.url">{{ link.url }}</a></small
            >
          </a>
        </div>
      </div>
      <div class="col-md-4">
        <Sidebar />
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "../components/Sidebar.vue";
import Breadcrumbs from "../components/Breadcrumbs.vue";
import Endpoints from "@/external/endpoints";

export default {
  name: "App",
  components: {
    Sidebar,
    Breadcrumbs,
  },
  data() {
    return {
      links: [],
    };
  },
  mounted() {
    Endpoints.getLinks().then((response) => (this.links = response.data.data));
  },
};
</script>

<style>
@import "../assets/styles/blog.scss";
</style>