import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Admin from '../views/Admin/Index.vue'
import Create from '../views/Articles/Create.vue'
import Edit from '../views/Articles/Edit.vue'
import ArticlesList from '../views/Articles/List.vue'
import ArticlesInspect from '../views/Articles/Inspect.vue'
import Links from '../views/Links.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: 'Home'
    }
  },
  {
    path: '/about',
    name: 'About',
    meta: {
      title: 'About',
      breadcrumb: [
        { name: 'Home', link: '/' },
        { name: 'About' }
      ]
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/admin',
    name: 'Admin',
    component: Admin,
    beforeEnter: requireAuth,
    meta: {
      title: "Admin"
    }
  },
  {
    path: '/links',
    name: 'Links',
    component: Links,
    meta: {
      title: 'Links',
      breadcrumb: [
        { name: 'Home', link: '/' },
        { name: 'Links' }
      ]
    }
  },
  {
    path: '/articles',
    name: 'articles',
    component: ArticlesList,
    meta: {
      title: 'Articles',
      breadcrumb: [
        { name: 'Home', link: '/' },
        { name: 'Articles' }
      ]
    },
  },
  {
    path: '/articles/:id',
    name: 'ArticleInspect',
    component: ArticlesInspect,
    meta: {
      title: 'Article',
      breadcrumb: [
        { name: 'Home', link: '/' },
        { name: 'Articles', link: '/articles' },
        { name: 'Article' }
      ]
    }
  },
  {
    path: '/articles/create',
    name: 'Create',
    component: Create,
    beforeEnter: requireAuth,
    meta: {
      title: "Create"
    }
  },
  {
    path: '/articles/edit/:id',
    name: 'Edit',
    component: Edit,
    beforeEnter: requireAuth,
    meta: {
      title: "Edit"
    }
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior() {
    return { top: 0, behavior: 'smooth' }
  }
})

const DEFAULT_TITLE = 'MuddyHiker';
router.afterEach((to) => {
  // Use next tick to handle router history correctly
  // see: https://github.com/vuejs/vue-router/issues/914#issuecomment-384477609

  if (to.meta.title) {
    document.title = DEFAULT_TITLE + " - " + to.meta.title;
  }
  else {
    document.title = DEFAULT_TITLE;
  }


});

function requireAuth() {
  if (localStorage.admin == null || localStorage.admin == false) {
    router.push("/");
  }
}



export default router
