<template>
  <div class="container">
    <div class="row g-5">
      <div class="col-md-8">
        <AdminHeader :icon="headerIcon" :title="headerTitle" />
        <AdminErrorMessage :errorMessages="errorMessages" />
        <AdminArticleManager />
      </div>
      <div class="col-md-4">
        <div class="p-4 mb-3 bg-light rounded border">
          <div class="admin-panel">
            <h4>Mods</h4>
            <ul>
              <li v-for="mod in mods" :key="mod.name">
                <i
                  v-if="mod.role == 'Admin'"
                  class="fas fa-crown"
                  style="color: gold"
                ></i>
                <i v-else class="fas fa-user"></i>
                {{ mod.name }}
              </li>
            </ul>
          </div>
        </div>
        <div class="p-4 mb-3 bg-light rounded border">
          <div class="admin-panel">
            <h4>Tags</h4>
            <input
              type="text"
              v-model="newTagTitle"
              @keyup.enter="newTag()"
              placeholder="Add Tag"
            />
            <ul>
              <li v-for="tag in tags" :key="tag.title">
                <i
                  class="far fa-times-circle"
                  @click="deleteTag(tag.tagId)"
                ></i>
                {{ tag.name }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-if="loadingMessages.length > 0">
    <transition name="modal">
      <div class="modal-mask">
        <div class="modal-wrapper">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title">Please wait, loading in progress</h5>
              </div>
              <div class="modal-body">
                <ul>
                  <li v-for="message in loadingMessages" :key="message">
                    {{ message[1] }}
                  </li>
                </ul>
              </div>
              <div class="modal-footer"></div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import AdminHeader from "../../components/AdminHeader.vue";
import AdminErrorMessage from "../../components/AdminErrorMessage.vue";
import AdminArticleManager from "../../components/AdminArticleManager.vue";
import Endpoints from "@/external/endpoints";

export default {
  name: "App",
  components: {
    AdminHeader,
    AdminErrorMessage,
    AdminArticleManager,
  },
  data() {
    return {
      tags: [],
      mods: [],
      test: [],
      articles: [],
      newTagTitle: null,
      featuredPostId: "na",
      errorMessages: [],
      postsLoadingMessage: "Loading Post Data...",
      loadingMessages: [],
      articleTitle: "",
      articleBody: "",
      articleSummary: "",
      articleSummaryImage: "",
      articleFeaturedImage: "",
      headerIcon: "fas fa-tools",
      headerTitle: "Article Admin",
    };
  },
  mounted() {
    this.loadingMessages.push([1, "Loading Tags"]);
    Endpoints.getTags()
      .then((response) => (this.tags = response.data))
      .catch((err) => {
        this.errorMessages.push("Tags: " + err);
      })
      .finally(() => {
        var index = this.loadingMessages.findIndex((x) => x[0] === 1);
        this.loadingMessages.splice(index, 1);
      });
  },
  methods: {
    deleteTag: function (id) {
      var index = this.tags.findIndex((x) => x.tagId === id);
      Endpoints.deleteTag(id).then(this.tags.splice(index, 1));
    },
    newTag: function () {
      let newTag = {
        tagId: 0,
        name: this.newTagTitle,
      };

      Endpoints.postTag(newTag).then((response) =>
        this.tags.push(response.data)
      );

      this.newTagTitle = null;
    },
  },
};
</script>

<style>
@import "../../assets/styles/blog.scss";

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
</style>
