<template>
  <div class="container">
    <Breadcrumbs :overRide="breadcrumbs" />
    <div class="row g-5">
      <div class="col-md-8">
        <Post :postData="post" />
      </div>
      <div class="col-md-4">
        <Sidebar />
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "../../components/Sidebar.vue";
import Post from "../../components/Post.vue";
import Breadcrumbs from "../../components/Breadcrumbs.vue";
import Endpoints from "@/external/endpoints";

export default {
  name: "App",
  components: {
    Sidebar,
    Post,
    Breadcrumbs,
  },
  data() {
    return {
      post: [],
      breadcrumbs: [],
    };
  },
  mounted() {
    this.loadArticle();
  },
  methods: {
    loadArticle() {
      if (this.$route.params.id) {
        Endpoints.getArticleBySlug(this.$route.params.id).then(
          (response) => (this.post = response.data.data)
        );
      }
    },
    updateTitle() {
      document.title = "MuddyHiker - " + this.post.title;
      this.breadcrumbs = this.$route.meta.breadcrumb;
      this.breadcrumbs[2].name = this.post.title;
    },
  },
  watch: {
    // call again the method if the route changes
    $route: "loadArticle",
    post: "updateTitle",
  },
};
</script>

<style>
@import "../../assets/styles/blog.scss";
</style>