<template>
  <div>
    <router-link to="/articles/create"
      ><button class="btn btn-success" style="float: right">
        Create
      </button></router-link
    >
    <h3>Exisitng Articles</h3>
  </div>
  <table class="table">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">Title</th>
        <th scope="col">Author</th>
        <th scope="col">Created</th>
        <th scope="col">
          <i class="fas fa-upload" title="Publish"></i>
        </th>
        <th scope="col">
          <i class="fas fa-search" title="Featured"></i>
        </th>
        <th scope="col">
          <i class="fas fa-list" title="Showcase"></i>
        </th>
        <th scope="col"></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="article in articles" :key="article.articleId">
        <th scope="row">{{ article.articleId }}</th>
        <td>{{ article.title }}</td>
        <td>{{ article.author }}</td>
        <td>{{ article.datePosted }}</td>
        <td>
          <input
            type="checkbox"
            @click="setPublished(article, $event)"
            :checked="article.published"
          />
        </td>
        <td>
          <input
            type="checkbox"
            @click="setFeaturedArticle(article, $event)"
            :checked="article.isFeatured"
          />
        </td>
        <td>
          <input
            type="checkbox"
            @click="setShowcaseArticle(article, $event)"
            :checked="article.isShowcased"
          />
        </td>
        <td>
          <router-link :to="this.getArticleUrl(article)"
            ><i class="fas fa-edit" title="Edit"></i
          ></router-link>
          &nbsp;
          <i
            class="fas fa-trash hover"
            title="Delete"
            @click="doDelete(article.articleId)"
          ></i>
        </td>
      </tr>
    </tbody>
  </table>
  <confirm-dialogue ref="confirmDialogue"></confirm-dialogue>
</template>
<script>
import ConfirmDialogue from "../components/ConfirmDialogue.vue";
import Endpoints from "@/external/endpoints";
export default {
  name: "AdminArticleManager",
  components: { ConfirmDialogue },
  data() {
    return {
      articles: [],
    };
  },
  props: {},
  mounted() {
    Endpoints.getArticles(1, 10, "", "", "", "").then(
      (response) => (this.articles = response.data.data)
    );
  },
  methods: {
    getArticleUrl(article) {
      return "/articles/edit/" + article.slug;
    },
    setFeaturedArticle: function (article, event) {
      article.isFeatured = event.target.checked;
      Endpoints.putArticle(article.articleId, article);
    },
    setPublished: function (article, event) {
      article.published = event.target.checked;
      Endpoints.putArticle(article.articleId, article);
    },
    setShowcaseArticle: function (article, event) {
      article.isShowcased = event.target.checked;
      Endpoints.putArticle(article.articleId, article);
    },
    async doDelete(id) {
      const ok = await this.$refs.confirmDialogue.show({
        title: "Delete Article",
        message:
          "Are you sure you want to delete this Article? It cannot be undone.",
        okButton: "Delete Forever",
      });
      // If you throw an error, the method will terminate here unless you surround it wil try/catch
      if (ok) {
        var index = this.articles.findIndex((x) => x.articleId === id);
        Endpoints.deleteArticle(id).then(this.articles.splice(index, 1));
      }
    },
  },
};
</script>
