<template>
  <div class="container">
    <Breadcrumbs />
    <FeaturedPost />
    <PostSummaryGrid />
    <div class="row g-5">
      <div class="col-md-8">
        <span v-if="this.errorMessage">{{ this.errorMessage }}</span>
        <div v-for="(post, x) in this.posts" :key="x">
          <Post v-bind:postData="post" />
        </div>
      </div>
      <div class="col-md-4">
        <Sidebar />
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "../components/Sidebar.vue";
import FeaturedPost from "../components/FeaturedPost.vue";
import PostSummaryGrid from "../components/PostSummaryGrid.vue";
import Post from "../components/Post.vue";
import Breadcrumbs from "../components/Breadcrumbs.vue";
import Endpoints from "@/external/endpoints";
import { ArticleParams } from "@/external/ArticleParams";

export default {
  name: "App",
  components: {
    Sidebar,
    FeaturedPost,
    PostSummaryGrid,
    Post,
    Breadcrumbs,
  },
  data() {
    return {
      posts: [],
      summaryLimit: 2,
      loadingMessage: "Loading Posts",
      errorMessage: null,
    };
  },
  mounted() {
    let param = new ArticleParams();
    Endpoints.getArticles(param).then(
      (response) => (
        (this.posts = response.data.data),
        (this.firstPage = response.data.firstPage),
        (this.previousPage = response.data.previousPage),
        (this.nextPage = response.data.nextPage),
        (this.lastPage = response.data.lastPage)
      )
    );
  },
  computed: {
    postSummaryData() {
      return this.summaryLimit
        ? this.posts.slice(0, this.summaryLimit)
        : this.posts;
    },
  },
};
</script>

