<template>
  <div class="container">
    <Breadcrumbs />
    <div class="row g-5">
      <div class="col-md-8">
        <h2>Articles</h2>
        <input
          type="text"
          class="form-control form-control-lg"
          placeholder="Search"
          v-model="searchWord"
          @change="searchArticles"
        />
        <br />
        <div class="list-group">
          <router-link
            v-for="(post, x) in this.posts"
            :key="x"
            :to="this.getArticleUrl(post)"
            class="list-group-item list-group-item-action"
          >
            <div class="">
              <img
                v-if="post.summaryImage"
                :src="post.summaryImage"
                class="img-thumbnail"
              />
              <strong class="d-inline-block text-primary">{{
                post.tag
              }}</strong>
              <h5>{{ post.title }}</h5>
              <div class="mb-1 text-muted small-text">
                <i class="fas fa-eye" title="Views"></i> {{ post.viewCount }} /
                <i class="fas fa-comment" title="Comments"></i> 0 /
                {{ post.datePosted }}
              </div>
              <p>
                {{ post.summary }}
              </p>
            </div>

            <small>Continue Reading...</small>
          </router-link>
        </div>
        <br />
        <nav
          v-if="this.posts.length > 0"
          class="blog-pagination"
          aria-label="Pagination"
        >
          <a
            v-if="previousPage"
            class="btn btn-outline-primary"
            @click="goToPreviousPage()"
            >Older</a
          >
          <a
            v-if="nextPage"
            class="btn btn-outline-primary"
            @click="goToNextPage()"
            >Newer</a
          >
        </nav>
      </div>
      <div class="col-md-4">
        <Sidebar />
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "../../components/Sidebar.vue";
import Breadcrumbs from "../../components/Breadcrumbs.vue";
import axios from "axios";
import Endpoints from "@/external/endpoints";

export default {
  name: "App",
  components: {
    Sidebar,
    Breadcrumbs,
  },
  data() {
    return {
      posts: [],
      firstPage: null,
      previousPage: null,
      nextPage: null,
      lastPage: null,
      searchWord: "",
    };
  },
  mounted() {
    Endpoints.getArticles(1, 10, "").then((response) =>
      this.buildDataFromResponse(response)
    );
  },
  methods: {
    getArticleUrl(article) {
      return "/articles/" + article.slug;
    },
    goToNextPage() {
      axios
        .get(this.nextPage)
        .then((response) => this.buildDataFromResponse(response));
    },
    goToPreviousPage() {
      axios
        .get(this.previousPage)
        .then((response) => this.buildDataFromResponse(response));
    },
    searchArticles() {
      Endpoints.getArticles(1, 10, this.searchWord).then((response) =>
        this.buildDataFromResponse(response)
      );
    },
    buildDataFromResponse(response) {
      this.posts = response.data.data;
      this.firstPage = response.data.firstPage;
      this.previousPage = response.data.previousPage;
      this.nextPage = response.data.nextPage;
      this.lastPage = response.data.lastPage;
    },
  },
  computed: {},
};
</script>

<style scoped>
@import "../../assets/styles/blog.scss";
img {
  max-width: 150px;
  float: right;
}
.small-text {
  font-size: small;
  margin-top: 10px;
}
</style>