<template>
  <div class="container">
    <div class="row g-5">
      <div class="col-md-8">
        <hr />
        <h2 class="blog-post-title">
          <i class="far fa-newspaper"></i> Create Article
        </h2>
        <hr />
        <div class="form-group">
          <input
            id="txtTitle"
            type="text"
            v-model="articleTitle"
            class="form-control form-control-lg"
            placeholder="Type a title here..."
          />
        </div>
        <br />

        <editor
          v-if="imageNames.length > 1"
          v-model="articleBody"
          api-key="bdmy24s8l0ju7xy4jgd1x7oh0wix99k001ltiir0grwtw5ai"
          :init="{
            height: 500,
            menubar: false,
            plugins: [
              'advlist autolink lists link image charmap print preview anchor',
              'searchreplace visualblocks code fullscreen',
              'insertdatetime media table paste code help wordcount',
            ],
            toolbar:
              'undo redo | formatselect | bold italic backcolor | \
           alignleft aligncenter alignright alignjustify | \
           bullist numlist outdent indent | removeformat | link image | code | help',
            image_caption: true,
            image_list: imageNames,
            content_style:
              'img { max-width: 100% !important; height: auto !important; margin-bottom: 10px; display: block; }, .float-right{ float: right;} ',
          }"
        />
      </div>
      <div class="col-md-4">
        <div class="p-4 mb-3 bg-light rounded border">
          <button
            @click="saveNewArticle"
            class="btn btn-success"
            style="float: right"
          >
            Save
          </button>
          <h3>Settings</h3>
          <div class="form-group">
            <label for="txtSummary">Article Summary</label>
            <textarea
              id="txtSummary"
              v-model="articleSummary"
              class="form-control"
              placeholder="Article summary text goes here..."
            />
          </div>
          <br />
          <div class="form-group">
            <label for="txtSummaryImage">Article Summary Image</label>
            <select
              id="slcSummaryImage"
              class="form-control"
              v-model="articleSummaryImage"
            >
              <option value="" disabled selected>Select Summary Image</option>
              <option
                v-for="imageName in imageNames"
                :key="imageName"
                v-bind:value="imageName.value"
              >
                {{ imageName.title }}
              </option>
            </select>
            <small class="form-text text-muted">Image size: 200 by 250.</small>
          </div>
          <br />
          <div class="form-group">
            <label for="txtFeaturedImage">Featured Summary Image</label>

            <select
              id="slcFeaturedImage"
              class="form-control"
              v-model="articleFeaturedImage"
            >
              <option value="" disabled selected>Select Featured Image</option>
              <option
                v-for="imageName in imageNames"
                :key="imageName"
                v-bind:value="imageName.value"
              >
                {{ imageName.title }}
              </option>
            </select>

            <small class="form-text text-muted"
              >Image size: 1500 by 1125.</small
            >
          </div>
          <br />
          <div class="form-group">
            <label for="txtSlug">Slug</label>
            <input
              id="txtSlug"
              type="text"
              v-model="articleSlug"
              class="form-control"
              placeholder="page-slug"
            />
            <small class="form-text text-muted">SEO friendly identity.</small>
          </div>
          <br />
          <div class="form-group">
            <select id="slcTag" class="form-control" v-model="articleTag">
              <option value="" disabled selected>Select a Tag</option>
              <option
                v-for="tag in tags"
                :key="tag.tagId"
                v-bind:value="tag.tagId"
              >
                {{ tag.name }}
              </option>
            </select>
          </div>
        </div>

        <div v-if="errorMessage" class="alert alert-danger">
          {{ errorMessage }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Endpoints from "@/external/endpoints";
import Editor from "@tinymce/tinymce-vue";
export default {
  name: "Create",
  components: {
    Editor,
  },
  data() {
    return {
      articleTitle: "",
      articleBody: "",
      articleSummary: "",
      articleSummaryImage: "",
      articleFeaturedImage: "",
      articleTag: "",
      articleSlug: "",
      tags: [],
      errorMessage: "",
      imageNames: [],
    };
  },
  mounted() {
    Endpoints.getTags().then((response) => (this.tags = response.data));
    Endpoints.getImageNames().then(
      (response) => (this.imageNames = response.data)
    );
  },
  methods: {
    saveNewArticle: function () {
      if (!this.validate()) {
        this.errorMessage = "All fields are required!";
        return false;
      }

      this.errorMessage = "";
      var article = {
        articleId: 0,
        author: "Martin",
        body: this.articleBody,
        featuredImage: this.articleFeaturedImage,
        summary: this.articleSummary,
        summaryImage: this.articleSummaryImage,
        tagId: this.articleTag,
        title: this.articleTitle,
        slug: this.articleSlug,
        userId: 1,
      };
      Endpoints.postArticle(article).then(this.$router.push("/admin"));
    },
    validate: function () {
      if (
        !this.articleBody ||
        !this.articleFeaturedImage ||
        !this.articleSummary ||
        !this.articleSummaryImage ||
        !this.articleTag ||
        !this.articleTitle ||
        !this.articleSlug
      ) {
        return false;
      }

      return true;
    },
  },
};
</script>
<style scoped>
</style>